import React from "react"
import PropTypes from "prop-types"

import Header from "./Header/header"
import "./layout.css"
import "./typography.css"

const Layout = ({ children }) => {
  return (
    <>
      <Header />
      <main>{children}</main>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
